var InvalidHex = function (givenHexString) {
    return new Error(givenHexString + " not of the form /(#)?[a-fA-F0-9]{3|4|6|8}/");
};
export var deconstructHexString = function (givenHexString) {
    var hexString = givenHexString.replace('#', '');
    var length = hexString.length;
    var charPerValue = length / 3;
    // Only support hex codes 3,4, 6 or 8 long for colour definition
    // This accounts for rgb[?a] syntax with a shortcut
    if ([3, 4, 6, 8].includes(length)) {
        var rgba = [];
        while (hexString) {
            var hexCode = hexString.slice(0, charPerValue);
            if (!/[a-fA-F0-9]{1,2}/.test(hexCode)) {
                throw InvalidHex(givenHexString);
            }
            rgba.push(hexCode);
            hexString = hexString.slice(charPerValue);
        }
        return rgba;
    }
    else {
        throw InvalidHex(givenHexString);
    }
};
